.TodoItem {
    background-color: #fafafa;
    border-radius: 8px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    transition: all 0.2s ease;
}
.TodoItem:hover{
    scale: 1.02;
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete {
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    color: #ccc;
}

.Icon-check {
    position: absolute;
    left: 12px;
    transition: all 0.2s ease;
}
.Icon-check:hover {
    color: #4caf50;
}

.Icon-check--active {
    color: #4caf50;
}

.Icon-delete {
    position: absolute;
    right: 12px;
    transition: all 0.2s ease;
}

.Icon-delete:hover {
    color: #ff0000;
}