.TodoSearch {
    background-color: #f9fbfc;
    border: 2px solid #202329;
    border-radius: 8px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1e1e1f;
    font-size: 24px;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    text-align: center;
}

.TodoSearch::placeholder {
    color: #a5a5a5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoSearch:focus {
    outline-color: #61dafb;
}